// 
import request from '@/utils/request'

// -
export function infoApi (data) {
  return request({
    url: '/user/index/info',
    method: 'POST',
    data
  })
}

// 
export function contListApi (data) {
  return request({
    url: '/user/index/share/cont_list',
    method: 'POST',
    data
  })
}



